body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'bariol_regular';
  src: url(/hacker-news/static/media/bariol_regular-webfont.c0df7400.eot), url(/hacker-news/static/media/bariol_regular-webfont.ceabb18a.woff) format('woff'),
    url(/hacker-news/static/media/bariol_regular-webfont.90c21e32.woff2) format('woff2'),
    url(/hacker-news/static/media/bariol_regular-webfont.ac437ca8.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: 'bariol_regular';
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
a {
  text-decoration: none;
  color: white;
}
.app-container {
  width: 100%;
  max-width: 85%;
  margin: 10px auto 0px auto;
}

.clearfix::before,
.clearfix::after {
  content: '';
  display: table;
  clear: both;
  overflow: auto;
}
.left {
  float: left;
}
.right {
  float: right;
}
.navbar {
  width: 100%;
  background-color: #4285f4;
  padding: 20px 5px;
  text-align: center;
  font-size: 40px;
}

.logo {
  font-weight: bold;
  vertical-align: middle;
  line-height: 26px;
}

.main {
  background-color: #1d1e22;
  width: 100%;
  padding: 0 5px 0 5px;
}

.post-item {
  display: table;
  width: 100%;
  border-bottom: #4285f4 solid 2px;
  padding: 20px 0px;
}
.post-position {
  width: 31px;
  font-size: 10pt;
  color: white;
  text-align: center;
}
.post-left {
  display: table-row;
  width: 51.28px;
}
.post-right {
  word-wrap: break-word;
}
.post-title {
  font-size: 10pt;
  padding-right: 5px;
  color: black;
}
.post-bottom-section {
  color: white;
  font-size: 8pt;
  padding-top: 3px;
}
.post-bottom-section > div {
  padding: 0 3px 0 3px;
}
.post-bottom-section > div:first-child {
  padding: 0 3px 0 0px;
}
.post-hide {
  border-right: 1px solid #828282;
  border-left: 1px solid #828282;
}

.post-new > div {
  padding: 0 3px 0 3px;
}

.story {
  padding-top: 15px;
  padding-left: 20px;
}
.story-title {
  font-size: 11pt;
  font-weight: 500;
  padding-right: 5px;
  color: black;
}
.story-item {
  width: calc(100% - 15px);
}

.spinner-container {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.story-comment-title {
  padding-top: 10px;
  text-align: center;
  color: white;
  font-size: 30px;
  font-weight: bold;
}

.story-time {
  color: white;
  margin-left: 2%;
  padding-left: 5px;
  border-left: 2px solid white;
}

.comment-list {
  margin-left: 2%;
  margin-top: 4px;
  padding-left: 5px;
  padding-bottom: 5px;
  border-left: 2px solid white;
  color: white;
}

.comment-list:hover {
  border-left: 2px solid #4285f4;
}

.child-comment {
  padding: 0 25px;
}

button {
  background: #4285f4;
  border: none;
  font-size: 1rem;
  font-weight: 300;
  padding: 15px 25px;
  border-radius: 4px;
  color: #fff;
}

.pagination-btn {
  padding: 5px 0;
  text-align: center;
}

.page-number {
  text-align: center;
  color: white;
  font-size: 50px;
  font-weight: bold;
}

.login-button {
  padding: 50px 50px;
  text-align: center;
  color: white;
}


@media only screen and (max-width: 768px) {
  .app-container {
    width: 100%;
    max-width: 100%;
    margin: 0px auto 0px auto;
  }
}
